<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container">
      <div class="row mt-md-5 mt-0">
        <div class="col-md-5 px-0 mb-3 mb-md-0">
          <div class="product_img" :style="{'backgroundImage':'url('+product.imageUrl+')'}"></div>
        </div>
        <div class="col-md-7">
          <h3 class id="exampleModalLabel font-weight-bold h3">{{ product.title }}</h3>
          <div class="mt-3">
            <p class="h4 text-info font-weight-bold">{{ product.content }}</p>
            <p class="h6">{{ product.description }}</p>
          </div>
          <div class="d-flex justify-content-start align-items-baseline">
            <del
              class="h6 mr-3"
              v-if="product.price<product.origin_price"
            >原價{{ product.origin_price | currency }} 元</del>
            <div class="h4 font-weight-bold" v-else>售價{{ product.origin_price | currency }} 元</div>
            <div
              class="h4 text-danger font-weight-bold"
              v-if="product.price<product.origin_price"
            >現在只要 {{ product.price | currency }} 元</div>
          </div>
          <div class="d-flex justify-content-end align-items-center my-3">
            <div class="mr-3">
              <select name id class="form-control" num="1" v-model="num">
                <!-- <option :value="null" selected disabled>請選擇人數</option> -->
                <option :value="num" v-for="num in 10" :key="num">選購{{num}}{{product.unit}}</option>
              </select>
              <!-- <div class="text-muted text-nowrap mr-3" v-if="product.num">
            小計
            <strong>{{product.num*product.price}}</strong>
            元
              </div>-->
            </div>
            <button
              type="button"
              class="btn btn-info"
              @click="addToCart(product.id,num)"
            >加入購物車</button>
          </div>
        </div>
      </div>
      <div class="aboutLike">
        <h2 class="h2 mb-3 mt-5 text-center">
          其他{{product.category}}行程
        </h2>
        <hr>
        <div class="row mt-4">
          <div
            class="col-md-4 mb-4"
            v-for="(item) in filterData"
            :key=" item.id"
            v-if="item.is_enabled===1 && item.category==product.category && item.title != product.title"
          >
            <div class="card border-0 shadow-sm">
              <a class="product_link" @click="getProduct(item.id)">
                <div class="item-img-outer">
                  <div class="item-img" :style="{backgroundImage:`url(${item.imageUrl})`}"></div>
                </div>

                <div class="card-body">
                  <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                  <h5 class="card-title font-weight-bold">{{item.title}}</h5>
                  <p class="card-text">{{item.content}}</p>
                  <div class="d-flex justify-content-between align-items-center">
                    <div class="d-flex justify-content-between align-items-baseline">
                      <!-- <div class="h5" v-if="!item.price">{{item.origin_price}}</div> -->
                      <del
                        class="h6 mr-3 mb-0"
                        v-if="item.price<item.origin_price"
                      >{{item.origin_price}}</del>
                      <div class="h5 font-weight-bold mb-0" v-else>{{item.origin_price}}</div>
                      <div
                        class="h5 text-danger font-weight-bold mb-0"
                        v-if="item.price<item.origin_price"
                      >{{item.price}}</div>
                    </div>
                    <button
                      type="button"
                      class="btn btn-outline-info rounded-circle addCart_btn"
                      @click.stop="addToCart(item.id)"
                    >
                      <i class="fas fa-spinner fa-spin" v-if="status.loadingItem ===item.id"></i>
                      <i class="fa fa-cart-plus" aria-hidden="true" v-else></i>
                    </button>
                  </div>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import Carousel from "../carousel";
export default {
  data() {
    return {
      productId: "",
      product: {},
      moreLook: [],
      status: {
        loadingItem: ""
      }
      // isLoading: false,
    };
  },
  computed: {
    filterData() {
      const vm = this;
      if (vm.searchText) {
        return vm.products.filter(item => {
          const data = item.title
            .toLowerCase()
            .includes(vm.searchText.toLowerCase());
          return data;
        });
      }
      return this.products;
    },
    isLoading() {
      return this.$store.state.isLoading;
    },
    cart() {
      return this.$store.state.cart;
    },
    products() {
      return this.$store.state.products;
    },
    categories() {
      return this.$store.state.categories;
    }
  },
  methods: {
    getProducts() {
      this.$store.dispatch("getProducts");
    },
    getProduct(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then(response => {
        vm.product = response.data.product;
        // $("#productModal").modal("show");
        vm.$store.dispatch("updateLoading", false);
        vm.status.loadingItem = "";
      });
    },
    addToCart(id, qty = 1) {
      this.$store.dispatch("addToCart", { id, qty });
    },
  },

  created() {
    this.getProducts();
    this.productId = this.$route.params.productId;
    this.getProduct(this.productId);
    this.getCart();
  }
};
</script>
<style lang="scss" scoped>
.product_img {
  height: 300px;
  background-size: cover;
  background-position: center center;
}
.productmenu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &_link {
    display: block;
    padding: 10px 5px;
    text-align: center;
    transition: 0.3s;
    width: 80px;
    color: #17a2b8;
    border-radius: 5px;
    min-width: 80px;
    margin: 0 10px;
    text-align: center;
    &.active {
      color: #fff;
      background: #17a2b8;
      text-decoration: none;
    }
    &:hover {
      color: #fff;
      background: #17a2b8;
      text-decoration: none;
    }
  }
}
.item-img-outer {
  height: 200px;
  width: 100%;
  overflow: hidden;
  position: relative;
  .heart-position {
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 10px;
  }
}
.item-img {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  position: relative;
  transition: 0.5s;
}
.product_link {
  display: block;
  cursor: pointer;
  &:hover {
    .item-img {
      transform: scale(1.3);
    }
  }
}
.card-text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.addCart_btn {
  line-height: 30px;
}

</style>


