<template>
  <div>
    <!-- <Alert></Alert> -->
    <!-- 右下角最愛列表 -->
    <div class="dropdown">
      <button
        class="btn btn-info dropdown-toggle"
        type="button"
        id="dropdownMenuButton"
        data-toggle="dropdown"
        aria-haspopup="true"
        aria-expanded="false"
      >
        <i class="fas fa-heart fa-lg favorite-icon"></i>
        <span class="badge badge-pill badge-danger favorite-qty">{{ favoriteQty }}</span>
      </button>
      <div
        class="dropdown-menu dropdown-menu-right p-3"
        aria-labelledby="dropdownMenuButton"
        style="min-width: 300px"
        data-offset="400"
      >
        <h6>最愛的行程</h6>
        <table class="table table-sm mb-0">
          <tbody>
            <!-- item.id 具唯一性，所以 key 使用 item.id -->
            <tr v-if="favoriteData ==''">
              <td class="text-danger text-center h5 py-3">這裡沒有東西</td>
            </tr>
            <tr v-for="(item) in favoriteData" :key="item.id">
              <td class="px-0">
                <button
                  type="button"
                  class="btn btn-sm text-danger"
                  @click.prevent="removefavorite(item)"
                >
                  <i class="fas fa-heart"></i>
                </button>
              </td>
              <td  class="text-left align-middle">{{ item.title }}</td>
              <!-- <td class="text-right align-middle">{{ item.price | currency }}</td> -->
              <td class="text-right align-middle">
                <button
                  type="button"
                  class="btn btn-outline-info btn-sm"
                  @click="addToCart(item.id)"
                >
                  <i class="fas fa-spinner fa-spin" v-if="item.id === loadingImg.loadingItem"></i>
                  <i class="fas fa-cart-plus" v-else></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>


<script>
// import Alert from './AlerMessage';
export default {
  name: "FavoriteList",
  // components: {
  //   Alert,
  // },
  props: {
    favoriteData: {},
    loadingImg: {}
  },
  methods: {
    openfavorite() {
      this.$emit("openfavorite");
    },
    // 將要移除的 ID 丟回給父元件
    removefavorite(item) {
      this.$emit("removefavorite", item);
    },
    // 從最愛清單加入購物車
    addToCart(id) {
      this.$emit("favoriteToCart", id);
    }
  },
  computed: {
    // 避免一開始資料沒讀進來會出錯，所以當有資料時才計算
    favoriteQty() {
      if (this.favoriteData) {
        return this.favoriteData.length;
      }
    }
  }
};
</script>
<style lang="scss">
.dropdown-toggle {
  position: relative;
  &::after {
    display: none;
  }
  .favorite-qty {
    position: absolute;
    top: -5px;
    right: -10px;
  }
}
.loveaddCart{
  padding: 4px 7px
}
</style>


