<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container">
      <Carousel />
      <div
        class="d-flex flex-lg-row flex-column justify-content-between  my-3"
      >
        <div class="productmenu d-flex justify-content-md-start justify-content-center">
          <a
            href="#"
            class="productmenu_link"
            @click.prevent="searchText = ''"
            :class="{ 'active': searchText === ''}"
          >全部顯示</a>
          <a
            class="productmenu_link"
            href="#"
            @click.prevent="searchText = item"
            :class="{ 'active': item === searchText}"
            v-for="item in categories"
            :key="item"
          >{{ item }}</a>
        </div>
        <div class="d-flex justify-content-between align-items-center">
        <form class="form-inline my-3 my-lg-0 mr-3">
          <div class="input-group">
            <input
              class="form-control"
              type="text"
              v-model="searchText"
              placeholder="Search"
              aria-label="Search"
            />
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button" @click="searchText = ''">
                <i class="fa fa-times"></i>
              </button>
            </div>
          </div>
        </form>
        <FavoriteList
          :favorite-data="favorite"
          :loading-img="status"
          @openfavorite="openFavoriteList(1)"
          @removefavorite="removeLove"
          @favoriteToCart="addToCart"
        ></FavoriteList>
        </div>

        <!-- <button class="btn btn-outline-primary ml-auto" type="button" @click="getProducts">重新取得資料</button> -->
      </div>

      <div class="row mt-4">
        <div
          class="col-md-4 mb-4"
          v-for="(item) in filterData"
          :key=" item.id"
          v-if="item.is_enabled===1"
        >
          <div class="card border-0 shadow-sm">
            <a class="product_link" @click="getProduct(item.id)">
              <div class="item-img-outer">
                <div class="item-img" :style="{backgroundImage:`url(${item.imageUrl})`}"></div>
                <div class="heart-position">
                  <button
                    type="button"
                    class="btn text-danger btn-sm"
                    @click.stop="removeLove(item)"
                    v-if="changeLove(item)"
                  >
                    <i class="fas fa-heart fa-2x"></i>
                  </button>
                  <button
                    type="button"
                    class="btn text-danger btn-sm"
                    @click.stop="addLove(item)"
                    v-else
                  >
                    <i class="far fa-heart fa-2x"></i>
                  </button>
                </div>
              </div>

              <div class="card-body">
                <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                <h5 class="card-title font-weight-bold">{{item.title}}</h5>
                <p class="card-text">{{item.content}}</p>
                <div class="d-flex justify-content-between align-items-center ">
                  <div class="d-flex justify-content-between align-items-end flex-wrap">
                    <!-- <div class="h5" v-if="!item.price">{{item.origin_price}}</div> -->
                    <del
                      class="h6 mr-2 mb-0"
                      v-if="item.price<item.origin_price"
                    >{{item.origin_price}}</del>
                    <div class="h5 font-weight-bold mb-0" v-else>{{item.origin_price}}</div>
                    <div
                      class="h5 text-danger font-weight-bold mb-0"
                      v-if="item.price<item.origin_price"
                    >{{item.price}}</div>
                  </div>
                  <button
                    type="button"
                    class="btn btn-outline-info"
                    @click.stop="addToCart(item.id)"
                  >
                    <i class="fas fa-spinner fa-spin" v-if="status.loadingItem ===item.id"></i>
                    <i class="fas fa-cart-plus" v-else></i>
                    加入購物車
                  </button>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>

    <!-- <Pagination v-bind:childPaginations="pagination" @changeCurrentPage="getProducts"></Pagination> -->
  </div>
</template>
<script>
import $ from "jquery";
import Carousel from "../carousel";
import Pagination from "../Pagination";
import FavoriteList from "../FavoriteList";
export default {
  data() {
    return {
      searchText: "",
      favorite: [],
      product: {},
      pagination: {},
      status: {
        loadingItem: ""
      },

      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      }
      // isLoading: false,
    };
  },
  computed: {
    filterData() {
      const vm = this;
      if (vm.searchText) {
        return vm.products.filter(item => {
          const data = item.title
            .toLowerCase()
            .includes(vm.searchText.toLowerCase());
          return data;
        });
      }
      return this.products;
    },
    isLoading() {
      return this.$store.state.isLoading;
    },
    products() {
      return this.$store.state.products;
    },
    categories() {
      return this.$store.state.categories;
    },
    cart() {
      return this.$store.state.cart;
    }
  },

  methods: {
    getProducts() {
      this.$store.dispatch("getProducts");
    },
    getProduct(id) {
      const vm = this;
      vm.$router.push(`/inpage/${id}`);
    },
    getProductCart(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then(response => {
        vm.product = response.data.product;
        // $("#productModal").modal("show");
        // console.log(response);
        vm.$store.dispatch("updateLoading", false);
        vm.status.loadingItem = "";
      });
    },
    getCart() {
      this.$store.dispatch("getCart");
    },
    addToCart(id, qty = 1) {
      this.$store.dispatch("addToCart", { id, qty });
    },
    getUnique() {
      const vm = this;
      const categories = new Set();
      vm.products.forEach(item => {
        categories.add(item.category);
      });
      vm.categories = Array.from(categories);
    },
    // 取得最愛列表
    getfavorite() {
      const vm = this;
      vm.favorite = JSON.parse(localStorage.getItem("savefavorite")) || [];
    },
    // 加入最愛
    addLove(item) {
      const vm = this;
      if (!vm.favorite.includes(item)) {
        vm.favorite.push(item);
        // vm.stared 存進 localStorage
        localStorage.setItem("savefavorite", JSON.stringify(vm.favorite));
      }
    },
    // 更改愛心標誌判斷
    changeLove(item) {
      const vm = this;
      return vm.favorite.some(el => {
        const result = item.id === el.id;
        return result;
      });
    },
    // 移除最愛
    removeLove(favoriteItem) {
      var vm = this;
      // 重新定位要刪除的 index
      vm.favorite.forEach(function(item, key) {
        if (favoriteItem.id === item.id) {
          vm.sliceIndex = key;
        }
      });
      vm.favorite.splice(vm.sliceIndex, 1);
      // vm.stared 存進 localStorage
      localStorage.setItem("savefavorite", JSON.stringify(vm.favorite));
    },
    // 開啟最愛列表
    openFavoriteList(open) {
      if (open == 1) {
        $(".favorite-dropdown").toggleClass("show");
      }
    }
  },

  components: {
    Carousel,
    Pagination,
    FavoriteList
  },

  created() {
    this.getProducts();
    this.getCart();
    this.getfavorite();
  }
};
</script>
<style lang="scss" scoped>
.productmenu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &_link {
    display: block;
    padding: 10px 5px;
    text-align: center;
    transition: 0.3s;
    width: 80px;
    color: #17a2b8;
    border-radius: 5px;
    min-width: 80px;
    margin: 0 10px;
    text-align: center;
    &.active {
      color: #fff;
      background: #17a2b8;
      text-decoration: none;
    }
    &:hover {
      color: #fff;
      background: #17a2b8;
      text-decoration: none;
    }
  }
}
.item-img-outer {
  height: 200px;
  width: 100%;
  overflow: hidden;
  position: relative;
  .heart-position {
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 10px;
  }
}
.item-img {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  position: relative;
  transition: 0.5s;
}
.product_link {
  display: block;
  cursor: pointer;
  &:hover {
    .item-img {
      transform: scale(1.3);
    }
  }
}
.card-text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>


