import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home'
import Login from '@/components/pages/Login';
import Dashboard from '@/components/Dashboard';
import Products from '@/components/pages/Products';
import Orders from '@/components/pages/Orders';
import Coupons from '@/components/pages/Coupons'
import CustomerOrder from '@/components/pages/CustomerOrders'
import CustomerCheckout from '@/components/pages/CustomerCheckout'
import User from '@/components/User';
import ItemList from '@/components/user/itemlist';
import UserOrder from '@/components/user/userorder'
import UserPay from '@/components/user/userpay';
import UserInfo from '@/components/user/userinfo';
import ItemInpage from '@/components/user/iteminpage'

// import ItemInpage from '@/components/user/ItemInpage';

Vue.use(Router);
export default new Router({
  routes: [{
      path: '/',
      name:'Home',
      component:Home
    },
    {
      path: '/',
      name: 'User',
      component: User,
      children: [{
         //商品列表
          path: 'list',
          name: 'ItemList',
          component: ItemList,
          // redirect:{name:'All'},
        },
        {   //商品頁
          path: 'inpage/:productId',
          name: 'ItemInpage',
          component: ItemInpage,
      },
        {   //結帳頁
            path: 'userpay/:orderId',
            name: 'UserPay',
            component: UserPay,
        },
        {   //訂單頁
            path: 'userorder',
            name: 'UserOrder',
            component: UserOrder,
        },
        {   //聯絡人頁
          path: 'userinfo',
          name: 'UserInfo',
          component: UserInfo,
      },
      ],
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [{
          path: 'products',
          name: 'Products',
          component: Products,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'orders',
          name: 'Orders',
          component: Orders,
          meta: {
            requiresAuth: true
          },
        },
        {
          path: 'coupons',
          name: 'Coupons',
          component: Coupons,
          meta: {
            requiresAuth: true
          },
        },
      ]
    },
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard,
      children: [{
          path: 'customer_order',
          name: 'CustomerOrder',
          component: CustomerOrder,
        },
        {
          path: 'customer_checkout/:orderId',
          name: 'CustomerCheckout',
          component: CustomerCheckout,
        },

      ]
    },
  ]
})



