<template>
  <div>

    <div class="container py-3">
      <div class="row">
        <div class="col-md-6 mx-auto">
          <!-- 購物車 -->
          <table class="table">
            <thead>
              <th width="40"></th>
              <th>品名</th>
              <th width="120">數量</th>
              <th width="100">單價</th>
            </thead>
            <tbody>
              <tr v-for="item in cart.carts" :key="item.id">
                <td class="align-middle">
                  <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCart(item.id)">
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
                <td class="align-middle">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">已套用優惠券</div>
                </td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td class="align-middle text-right">{{ item.final_total }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="3" class="text-right">總計</td>
                <td class="text-right">{{ cart.total }}</td>
              </tr>
              <tr v-if="cart.final_total !==cart.total">
                <td colspan="3" class="text-right text-success">折扣價</td>
                <td class="text-right text-success">{{ cart.final_total }}</td>
              </tr>
            </tfoot>
          </table>
          <!-- 優惠券 -->
          <div class="input-group mb-3 input-group-sm">
            <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼" />
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button" @click="addCouponCode">套用優惠碼</button>
            </div>
          </div>
        </div>
        <div class="col-md-6">
          <form @submit.prevent="createOrder">
            <div class="form-group">
              <label for="useremail">Email</label>
              <input type="email" class="form-control" name="email" id="useremail" v-validate="'required|email'"
                :class="{'is-invalid':errors.has('email')}" v-model="form.user.email" placeholder="請輸入 Email" />
              <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
            </div>

            <div class="form-group">
              <label for="username">收件人姓名</label>
              <input type="text" class="form-control" name="name" id="username"
                :class="{'is-invalid':errors.has('name')}" v-model="form.user.name" placeholder="輸入姓名"
                v-validate="'required'" />
              <span class="text-danger" v-if="errors.has('name')">請輸入姓名</span>
            </div>

            <div class="form-group">
              <label for="usertel">收件人電話</label>
              <input type="tel" class="form-control" id="usertel" name="tel" :class="{'is-invalid':errors.has('tel')}"
                v-validate="'required'" v-model="form.user.tel" placeholder="請輸入電話" />
              <span class="text-danger" v-if="errors.has('tel')">請輸入電話</span>
            </div>

            <div class="form-group">
              <label for="useraddress">收件人地址</label>
              <input type="text" class="form-control" name="address" id="useraddress"
                :class="{'is-invalid':errors.has('address')}" v-validate="'required'" v-model="form.user.address"
                placeholder="請輸入地址" />
              <span class="text-danger" v-if="errors.has('address')">請輸入地址</span>
            </div>

            <div class="form-group">
              <label for="comment">留言</label>
              <textarea name id="comment" class="form-control" cols="30" rows="5" v-model="form.message"></textarea>
            </div>
            <div class="text-center">
              <button class="btn btn-danger">送出訂單</button>
            </div>
          </form>
        </div>

      </div>


    </div>


