<template>
  <div>
      <footer class="bg-info text-center py-3 text-white">
          ©Copyright 2020. TangYuCheng
      </footer>
  </div>
</template>
<script>
export default {
  name: "Footer",
  methods: {
    signout() {
      const vm = this;
      const url = `${process.env.APIPATH}/logout`;
      this.$http.post(url).then(response => {
        if (response.data.success) {
          vm.$router.push("/signin");
        }
      });
    }
  }
};
</script>


