<template>
  <div>
    <header class="bg-info">
      <div class="container">
        <nav class="navbar navbar-expand-md py-0 bg-info">
          <router-link class="header_title mr-3" href="#" to="/">AirTrip</router-link>
          <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
            aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <i class="fas fa-bars"></i>
          </button>
          <div class="collapse navbar-collapse" id="navbarNav">
            <ul class="navbar-nav">
              <li class="nav-item" :class="{'active':pathCheck=='/list'}">
                <router-link class="nav-link" href="#" to="/list">商品列表</router-link>
              </li>
              <li class="nav-item" :class="{'active':pathCheck=='/userorder'}">
                <router-link class="nav-link" href="#" to="/userorder">購物列表</router-link>
              </li>
              <li class="nav-item" :class="{'active':pathCheck=='/login'}">
                <router-link class="nav-link" href="#" to="/login">會員登入</router-link>
              </li>
            </ul>
            <div class="dropdown ml-auto text-right">
              <button class="btn btn-sm btn-cart" data-toggle="dropdown" data-flip="false">
                <i class="fa fa-shopping-cart text-white fa-2x" aria-hidden="true"></i>
                <span class="badge badge-pill badge-danger">{{cart.carts.length}}</span>
                <span class="sr-only">unread messages</span>
              </button>
              <div class="dropdown-menu dropdown-menu-right p-3" style="min-width: 300px" data-offset="400">
                <h6>已選擇商品</h6>
                <table class="table table-sm">
                  <tbody>
                    <tr v-for="item in cart.carts" :key="item.id" v-if="cart.carts.length">
                      <td class="align-middle text-center">
                        <a href="#" class="text-muted" @click.prevent="removeCart(item.id)">
                          <i class="far fa-trash-alt"></i>
                        </a>
                      </td>
                      <td class="align-middle">{{ item.product.title }}</td>
                      <td class="align-middle">{{ item.qty }}{{item.product.unit}}</td>
                      <td class="align-middle text-right">{{item.total}}</td>
                    </tr>
                  </tbody>
                </table>
                <router-link class="btn btn-warning btn-block" to="/userorder">
                  <i class="fa fa-cart-plus" aria-hidden="true"></i> 結帳去
                </router-link>
              </div>
            </div>
          </div>
        </nav>
      </div>
    </header>
  </div>
</template>
<script>
  export default {
    name: "Header",
    data() {
      return {
        // cart: {
        //   carts: []
        // },
        // isLoading: false
      };
    },
    computed: {
      pathCheck() {
        return this.$route.path;
      },
      cart() {
        return this.$store.state.cart;
      }
    },
    methods: {
      getCart() {
        this.$store.dispatch('getCart');
      },
      // removeCart(id) {
      //   const vm = this;
      //   const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      //   vm.$store.dispatch('updateLoading', true);
      //   this.$http.delete(url).then(response => {
      //     vm.$store.dispatch('updateLoading', false);
      //     vm.getCart();
      //     console.log("刪除購物車項目", response);
      //   });
      // }
      removeCart(id) {
        this.$store.dispatch("removeCart", id);
      },
    },
    created() {
      this.getCart();
    }
  };
</script>
<style lang="scss" scoped>
  .header {
    &_title {
      font-size: 1.8rem;
      font-style: italic;
      font-weight: bold;
      color: #fff;

      &:hover {
        text-decoration: none;
        color: #fff;
      }
    }
  }

  .nav {
    &-item {
      padding: 5px;
      transition: 0.3s;
      text-align: center;

      .nav-link {
        color: #fff;
      }

      &:hover {
        background: rgb(22, 106, 152);
      }

      &.active {
        background: rgb(22, 106, 152);
      }
    }
  }

  .navbar-toggler {
    border: 2px solid #fff;
    padding: .25rem .5rem;

    i {
      color: #fff;
    }
  }

  /* 半透明背景 */
  .bg-lighter {
    background-color: rgba(255, 255, 255, 0.45);
  }

  /* 購物車按鈕 */
  .btn-cart {
    background-color: transparent;
    position: relative;
  }

  /* 購物車按鈕定位 */
  .btn-cart .badge {
    position: absolute;
    top: 1px;
    right: 1px;
  }

  .main-content {
    min-height: calc(100vh - 56px - 176px);
  }

  .box-shadow {
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.05);
    transition: 0.3s linear;
  }

  .box-shadow:hover {
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.08);
  }

  .dropdown-menu-right {
    right: 0;
    left: auto;
  }
  .alert-rounded {
    border-radius: 50px;
  }
</style>


