<template>
  <div>
    <table class="table mt-4" v-if="orders.length">
      <thead>
        <tr>
          <th>購買時間</th>
          <th>Email</th>
          <th>購買款項</th>
          <th>應付金額</th>
          <th>是否付款</th>
          <!-- <th>刪除</th> -->
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in sortOrder" :key="item.id" :class="{'text-secondary':!item.is_paid}">
          <td>{{ item.create_at | date }}</td>
          <td>
            <span v-if="item.user.email" v-text="item.user.email"></span>
          </td>
          <td>
            <ul>
              <li v-for="(product,i) in item.products" :key="i">
                {{product.product.title}}數量：{{product.qty}}
                {{product.product.unit}}
              </li>
            </ul>
          </td>
          <td class="text-right">{{item.total | currency}}</td>
          <td>
            <span v-if="item.is_paid" class="text-success">已付款</span>
            <span v-else class="text-danger">未付款</span>
          </td>
          <!-- <td>
            <button class="btn btn-outline-danger btn-sm" @click="openDelModal(item)">刪除</button>
          </td> -->
        </tr>
      </tbody>
    </table>
    <Pagination v-bind:childPaginations="pagination" @changeCurrentPage="getOrder"></Pagination>

    <!-- <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除
            <strong class="text-danger"></strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="deleteProduct">確認刪除</button>
          </div>
        </div>
      </div>
    </div> -->
  </div>
</template>
<script>
import $ from "jquery";
import Pagination from "../Pagination";
export default {
  data() {
    return {
      orders: [],
      pagination: {},
      // isLoading: false
    };
  },

  methods: {
    getOrder(currentPage = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${currentPage}`;
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then(response => {
        vm.$store.dispatch('updateLoading', false);
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
      });
    },
    openDelModal(item) {
      this.tempProduct = item;
      $("#delProductModal").modal("show");
    },
    // removeCartItem(id) {
    //   const vm = this;
    //   const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
    //   vm.isLoading = true;
    //   this.$http.delete(url).then((response) => {
    //     console.log(response);
    //     vm.isLoading = false;
    //     vm.getCart();
    //   });
    // },
  },
  computed: {
    sortOrder() {
      const vm = this;
      let newOrder = [];
      if (vm.orders.length) {
        newOrder = vm.orders.sort((a, b) => {
          const aIsPaid = a.is_paid ? 1 : 0;
          const bIsPaid = b.is_paid ? 1 : 0;
          return bIsPaid - aIsPaid;
        });
      }
      return newOrder;
    },
        isLoading(){
      return this.$store.state.isLoading;
    }
  },
  components: {
    Pagination
  },
  created() {
    this.getOrder();
  }
};
</script>


